import { useAppState } from '../contexts/AppState'
import { replaceIndex } from '../reducer-util'
import EncoderBinding from './EncoderBinding'

function SensorBindings ({ layer, sensors, onUpdate }) {
  const [keymap] = useAppState('keyboard.keymap')
  const { sensorBindings = [] } = keymap.layers[layer]

  function handleUpdate (sensorIndex, bindIndex, binding) {
    // TODO: handle sensor bindings list that doesn't match sensor count
    const bind = binding.params[0]
    const originalBindings = sensorBindings[sensorIndex] || {
      value: '&inc_dec_kp',
      params: [
        { value: '', params: [] },
        { value: '', params: [] }
      ]
    }

    onUpdate(sensorIndex, {
      ...originalBindings,
      params: replaceIndex(originalBindings.params, bindIndex, bind)
    })
  }

  return (
    <div style={{ margin: '0 0 30px' }}>
      <h3>Sensor bindings</h3>
      <div style={{
        display: 'flex',
        flexDirection: 'row',
        gap: '25px'
      }}>
        {sensors.map((sensor, i) => {
          const labelText = sensor.label || sensor.identifier
          const [increment, decrement] = sensorBindings[i]?.params || []
          return (
            <div key={i} style={{ display: 'block' }}>
              <label style={{ fontVariant: 'small-caps', fontSize: '90%' }}>
                <code>{labelText}</code>
              </label>
              <div style={{
                display: 'flex',
                flexDirection: 'row',
                gap: '5px',
                marginTop: '20px'
              }}>
                <EncoderBinding
                  direction="ccw"
                  params={[decrement]}
                  onUpdate={bind => handleUpdate(i, 1, bind)}
                />
                <EncoderBinding
                  direction="cw"
                  params={[increment]}
                  onUpdate={bind => handleUpdate(i, 0, bind)}
                />
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default SensorBindings
