import { useEffect } from 'react'

import { loadKeyboard } from '../../api'

function LocalPicker ({ onSelect }) {
  useEffect(() => {
    (async function() {
      const { layout, keymap, sensors } = await loadKeyboard()
      onSelect({ source: 'local', layout, keymap, sensors })
    })()
  }, [])
  return null
}

export default LocalPicker
