import times from 'lodash/times'
import { withoutIndex, replaceIndex } from '../reducer-util'

const DEFAULT_BIND_BEHAVIOUR = '&trans'
const createEmptyBinding = (value = DEFAULT_BIND_BEHAVIOUR) => ({
  value, params: []
})

const createEmptyLayer = (length) => ({
  bindings: times(length, () => createEmptyBinding())
})


export default function keymapReducer (state, action = {}) {
  const { type } = action
  const { layers, layer_names: names } = state

  switch (type) {
    case 'CREATE_LAYER':
      return {
        ...state,
        layer_names: [
        ...names,
          `Layer #${layers.length}`
        ],
        layers: [
          ...layers,
          createEmptyLayer(action.bindingCount)
        ]
      }

    case 'RENAME_LAYER':
      return {
        ...state,
        layer_names: replaceIndex(
          names,
          action.index,
          action.name
        )
      }

    case 'DELETE_LAYER':
      return {
        ...state,
        layer_names: withoutIndex(names, action.index),
        layers: withoutIndex(layers, action.index)
      }

    case 'UPDATE_KEY_BINDING':
      return {
        ...state,
        layers: replaceIndex(
          layers,
          action.layerIndex,
          {
            ...layers[action.layerIndex],
            bindings: replaceIndex(
              layers[action.layerIndex].bindings,
              action.bindIndex,
              action.binding
            )
          }
        )
      }

    case 'UPDATE_SENSOR_BINDINGS':
      return {
        ...state,
        layers: replaceIndex(
          layers,
          action.layerIndex,
          {
            ...layers[action.layerIndex],
            sensorBindings: replaceIndex(
              layers[action.layerIndex].sensorBindings || [],
              action.sensorIndex,
              action.bindings
            )
          }
        )
      }

    default:
      throw new Error('unsupported action', type)
  }
}
