
export default function combosReducer (state, action) {
  const { type } = action

  switch (type) {
    case 'UPDATE_COMBO_BINDING':
      return [
        ...state.slice(0, action.comboIndex),
        { ...state[action.comboIndex], bindings: action.bindings },
        ...state.slice(action.comboIndex + 1)
      ]

    case 'UPDATE_COMBO_KEYPOSITIONS':
      return [
        ...state.slice(0, action.comboIndex),
        { ...state[action.comboIndex], keyPositions: action.keyPositions },
        ...state.slice(action.comboIndex + 1)
      ]

    case 'CREATE_COMBO':
      return [
        ...state,
        {
          identifier: 'new_combo',
          keyPositions: [],
          bindings: [{
            value: '&trans',
            params: []
          }]
        }
      ]

    case 'RENAME_COMBO':
      return [
        ...state.slice(0, action.index),
        { ...state[action.index], identifier: action.name },
        ...state.slice(action.index + 1)
      ]

    case 'DELETE_COMBO':
      return [
        ...state.slice(0, action.index),
        ...state.slice(action.index + 1)
      ]

    case 'CUSTOMIZE_COMBO':
      return [
        ...state.slice(0, action.index),
        { ...state[action.index], ...action.config },
        ...state.slice(action.index + 1)
      ]

    default:
      throw new Error(`unexpected action ${type}`)
  }
}
