import '@fortawesome/fontawesome-free/css/all.css'
import keyBy from 'lodash/keyBy'
import { useCallback, useEffect, useMemo } from 'react'

import './App.css';
import { apiBaseUrl } from './config'
import { DefinitionsContext, SettingsContext } from './providers'
import { loadKeycodeGlyphs, loadKeycodes } from './keycodes'
import { loadBehaviourBindings } from './api'

import IconButton from './Common/IconButton'
import Loader from './Common/Loader'
import Spinner from './Common/Spinner';
import Keyboard from './Keyboard/Keyboard'
import GitHubLink from './GitHubLink'
import Settings from './Dialogs/Settings'
import SourcePicker from './Sources/SourcePicker'
import github from './Sources/Github/api'
import BuildLink from './Sources/Github/BuildLink'
import Commit from './Sources/Github/Dialogs/Commit'

import { useAppState } from './contexts/AppState'
import { filter } from 'lodash';
import ErrorBoundary from './ErrorBoundary'

function App() {
  const [state, dispatch] = useAppState()
  const {
    definitions,
    source,
    sourceOther,
    keyboard,
    _unmodifiedKeymap,
    saving,
    theme,
    settingsVisible,
    experimentalSettings,
    latestBuild
  } = state

  useEffect(() => {
    if (!sourceOther?.github) {
      dispatch({ type: 'GITHUB_BUILD_UPDATE', build: null })
      return
    }

    function handler (run) {
      if (run.head_branch === sourceOther.github.branch) {
        dispatch({ type: 'GITHUB_BUILD_UPDATE', build: run })
      }
    }

    ;(async function () {
      const { repository, branch } = sourceOther.github
      handler(await github.fetchBuildStatus(repository, branch))
    })()

    github.on('build-update', handler)
    return () => github.off('build-update', handler)
  }, [sourceOther, dispatch])

  function openCommitDialog () {
    dispatch({ type: 'COMMIT_DIALOG_OPEN' })
  }

  function handleCompile() {
    const query = new URLSearchParams({})
    if (!state.experimentalSettings.legacyKeymap) {
      query.set('useDevicetree', 'true')
    }

    fetch(`${apiBaseUrl}/keymap?${query.toString()}`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(state.keyboard?.keymap)
    })
  }

  const handleCommitChanges = useCallback(function(message) {
    const { repository, branch } = sourceOther.github

    ;(async function () {
      dispatch({ type: 'COMMIT_DIALOG_CLOSE' })
      dispatch({ type: 'KEYMAP_SAVE_START' })

      await github.commitChanges(repository, branch, keyboard.layout, keyboard.keymap, {
        useDevicetree: !experimentalSettings.legacyKeymap,
        message
      })

      dispatch({ type: 'KEYMAP_SAVE_SUCCESS' })
    })()
  }, [sourceOther, keyboard, experimentalSettings, dispatch])

  const handleKeyboardSelected = useCallback(function(event) {
    const { source, layout, keymap, sensors = [], ...other } = event
    dispatch({
      type: 'SELECT_KEYBOARD',
      source,
      sourceOther: other,
      layout,
      keymap,
      sensors
    })
  }, [dispatch])

  // TODO: move this to another provider
  // it's only used here for the <Loader /> which probably isn't that useful.
  const initialize = useMemo(() => {
    return async function () {
      const [keycodes, behaviours, glyphs] = await Promise.all([
        loadKeycodes(),
        loadBehaviourBindings(),
        loadKeycodeGlyphs()
      ])

      keycodes.indexed = keyBy(keycodes, 'code')
      behaviours.indexed = keyBy(behaviours, 'code')
      behaviours.context = {
        none: behaviours.filter(({ context }) => !context),
        macros: filter(behaviours, { context: 'macros' })
      }
      behaviours.byContext = {
        none: keyBy(behaviours.context.none, 'code'),
        macros: keyBy(behaviours.context.macros, 'code')
      }

      dispatch({
        type: 'DEFINITIONS_SET',
        definitions: { keycodes, behaviours, glyphs }
      })
    }
  }, [dispatch])

  const handleUpdateSettings = useCallback(function (settings) {
    dispatch({ type: 'SETTINGS_UPDATE', settings})
  }, [dispatch])

  const openSettingsDialog = useCallback(
    () => dispatch({ type: 'SETTINGS_OPEN' }),
    [dispatch]
  )

  const hasChanged = keyboard && !Object.is(keyboard.keymap, _unmodifiedKeymap)

  return (
    <>
      <ErrorBoundary>
        <Loader load={initialize}>
          <SettingsContext.Provider value={{
            settings: experimentalSettings,
            openSettingsDialog
          }}>
            <SourcePicker onSelect={handleKeyboardSelected} />

            <div id="settings-bar">
              <IconButton
                id="light-mode"
                title="Toggle theme"
                icon={{
                  light: 'sun',
                  dark: 'moon',
                  system: 'circle-half-stroke'
                }[theme] || 'circle-half-stroke'}
                onClick={() => dispatch({ type: 'CYCLE_THEME' })}
              />

              <IconButton
                id="settings-button"
                icon="cog"
                onClick={openSettingsDialog}
              />
              {settingsVisible && (
                <Settings
                  settings={experimentalSettings}
                  onUpdate={handleUpdateSettings}
                  onClose={() => dispatch({ type: 'SETTINGS_CLOSE' })}
                />
              )}
            </div>

            <div id="actions">
              {source === 'local' && (
                <button disabled={!hasChanged} onClick={handleCompile}>
                  Save Local
                </button>
              )}
              {source === 'github' && (
                <button
                  title="Commit keymap changes to GitHub repository"
                  disabled={!hasChanged}
                  onClick={openCommitDialog}
                >
                  {saving ? 'Saving' : 'Commit Changes'}
                  {saving && <Spinner />}
                </button>
              )}
              {state.commitDialogOpen && (
                <Commit
                  onConfirm={handleCommitChanges}
                  onCancel={() => dispatch({ type: 'COMMIT_DIALOG_CLOSE' })}
                />
              )}

              {latestBuild && <BuildLink latestRun={latestBuild} />}
            </div>
            <DefinitionsContext.Provider value={definitions}>
              {keyboard && (
                <Keyboard
                  layout={keyboard.layout}
                  keymap={keyboard.keymap}
                  sensors={keyboard.sensors}
                />
              )}
            </DefinitionsContext.Provider>
          </SettingsContext.Provider>
        </Loader>
      </ErrorBoundary>
      <GitHubLink />
    </>
  );
}

export default App;
