import DialogBox from '../Common/DialogBox'
import Link from '../Common/Link'
import Modal from '../Common/Modal'

const wikiUrl = 'https://github.com/nickcoutsos/keymap-editor/wiki/Defining-keyboard-layouts'

function SuggestAutoLayoutTweak (props) {
  const wikiLink = (
    <Link href={wikiUrl} external={true}>
      Keymap Editor Wiki: Defining keyboard layouts
    </Link>
  )

  return (
    <Modal>
      <DialogBox {...props}>
        <h2>This layout was Auto-Generated</h2>
        <p>
          Hi there! Your repository didn't include a layout definition in
          the <code>info.json</code> file, so the keymap editor tried to
          generate one automatically in a last-ditch effort to help you get into
          the app.
        </p>
        <p>
          This layout definition was saved to your repository when you first
          used it with the keymap editor, so remember that if there are any
          tweaks you'd like to make to improve it you can do so by editing
          the <code>info.json</code> file.
        </p>
        <p>
          For more information on how to do that see {wikiLink}. If you run into
          any trouble, reach out by making an issue on the project repo and I'd
          be happy to discuss.
        </p>
      </DialogBox>
    </Modal>
  )
}

SuggestAutoLayoutTweak.propTypes = {}

export default SuggestAutoLayoutTweak
