import { useContext } from 'react'

import styles from './styles.module.css'
import { SettingsContext } from '../providers'

function FeatureRequiresDevicetree () {
  const { openSettingsDialog } = useContext(SettingsContext)
  const settingsLink = (
    <span onClick={openSettingsDialog} className={styles.linkish}>
      Settings
    </span>
  )

  return (
    <div style={{ maxWidth: '500px', margin: '80px auto 0' }}>
      <h2>⚠️ Devicetree Parsing Required</h2>
      <p>
        This feature requires reading and writing your <code>.keymap</code> file
        directly, instead of re-generating it. To allow this you must disable
        the "legacy keymap" feature.
      </p>
      <p>
        See the {settingsLink} dialog for more details.
      </p>
    </div>
  )
}

export default FeatureRequiresDevicetree
