import styles from './dialog-box.module.css'

export default function DialogBox(props) {
  const { dismissText = 'Ok', onDismiss, children, ...rest } = props

  return (
    <div className={styles.dialog} {...rest}>
      {children}
      {dismissText && (
        <button className={styles.button} onClick={onDismiss}>
          {dismissText}
        </button>
      )}
    </div>
  )
}
