import { useMemo } from 'react'
import PropTypes from 'prop-types'

import styles from './styles.module.css'
import KeyboardLayout from '../KeyboardLayout'
import KeyWrapper from '../Keys/KeyWrapper'
import * as layoutPropTypes from '../layoutPropTypes'
import { getWrapperStyle } from '../util'

function SelectKeyCTA () {
  return (
    <p className={styles.floatingMessage}>
      Click on a key to add it to the combo
    </p>
  )
}

function ComboInputMap ({ layout, keyPositions, onUpdate }) {
  const wrapperStyle = useMemo(() => getWrapperStyle(layout, {
    overrides: { padding: 0, margin: 0, position: 'relative' },
    scale: 0.2,
  }), [layout])

  const handleSelectKey = index => {
    const isSelected = keyPositions.includes(index)
    onUpdate(
      isSelected
        ? keyPositions.filter(keyPosition => keyPosition !== index)
        : [...keyPositions, index]
    )
  }

  return (
    <div className={styles.comboInputMap} style={wrapperStyle}>
      <KeyboardLayout
        layout={layout}
        bindings={[]}
        scale={0.2}
        renderKey={({ index }) => (
          <KeyWrapper
            onClick={() => handleSelectKey(index)}
            themeClass={[
              styles.selectable,
              keyPositions.includes(index)
                ? styles.highlight
                : ''
            ].join(' ')}
          />
        )}
      />
      {keyPositions.length < 2 && <SelectKeyCTA />}
    </div>
  )
}

ComboInputMap.propTypes = {
  layout: layoutPropTypes.layout.isRequired,
  onUpdate: PropTypes.func.isRequired,
  keyPositions: PropTypes.arrayOf(
    PropTypes.number
  ).isRequired,
}

export default ComboInputMap
