import { useContext, useState } from 'react'

import ComboInputMap from './ComboInputMap'
import styles from './styles.module.css'
import Key from '../Keys/Key'
import * as layoutPropTypes from '../layoutPropTypes'
import { useAppState } from '../../contexts/AppState'
import ActionButton from '../../Common/ActionButton'
import Icon from '../../Common/Icon'
import Link from '../../Common/Link'
import EditableField from '../../Common/EditableField'
import { SearchContext, SettingsContext } from '../../providers'
import FeatureRequiresDevicetree from '../../Dialogs/FeatureRequiresDevicetree'
import ConfigModal from './ConfigModal'
import { validateDevicetreeIdentifier } from '../validators'

function ComboEditor (props) {
  const { settings } = useContext(SettingsContext)
  const { identifiers } = useContext(SearchContext)
  const [combos, dispatch] = useAppState('keyboard.keymap.combos')
  const [configEdit, setConfigEdit] = useState(null)

  const { layout } = props

  function validateComboIdentifier (value) {
    return validateDevicetreeIdentifier(value) || (
      identifiers.includes(value)
        ? `Cannot use existing identifier "${value}"`
        : null
    )
  }

  function handleUpdateBindings (i, update) {
    dispatch({
      type: 'UPDATE_COMBO_BINDING',
      comboIndex: i,
      bindings: [update]
    })
  }

  function handleUpdateKeyPositions (i, update) {
    dispatch({
      type: 'UPDATE_COMBO_KEYPOSITIONS',
      comboIndex: i,
      keyPositions: update
    })
  }

  function handleRenameCombo (index, name) {
    dispatch({ type: 'RENAME_COMBO', index, name })
  }

  function handleAddCombo () {
    dispatch({ type: 'CREATE_COMBO' })
  }

  function handleDeleteCombo (index) {
    dispatch({ type: 'DELETE_COMBO', index })
  }

  function handleCustomizeCombo (index, config) {
    setConfigEdit(null)
    dispatch({ type: 'CUSTOMIZE_COMBO', index, config })
  }

  if (settings.legacyKeymap) {
    return (
      <FeatureRequiresDevicetree />
    )
  }

  return (
    <div className={styles.main}>
      {configEdit !== null && (
        <ConfigModal
          combo={combos[configEdit]}
          onUpdate={config => handleCustomizeCombo(configEdit, config)}
        />
      )}

      <div className={styles.sidebar}>
        <Link external href="https://zmk.dev/docs/features/combos">
          <Icon name="book" /> ZMK Docs: Combos
        </Link>

        <ActionButton onClick={handleAddCombo}>
          Add New Combo
        </ActionButton>
      </div>

      <div className={styles.comboPanel}>
        {combos.map((combo, i) => (
          <div key={i} className={styles.combo}>
            <div className={styles.heading}>
              <EditableField
                value={combo.identifier}
                validate={validateComboIdentifier}
                render={children => <h5><code>{children}</code></h5>}
                onUpdate={name => handleRenameCombo(i, name)}
              />
              <div className={styles.actions}>
                <Icon
                  name="wrench"
                  className={`${styles.action}`}
                  onClick={() => setConfigEdit(i)}
                />
                <Icon
                  name="trash-can"
                  className={`${styles.action} ${styles.remove}`}
                  onClick={() => handleDeleteCombo(i)}
                />
              </div>
            </div>

            <div className={styles.detail}>
              <div className={styles.comboOutput}>
                <Key
                  {...combo.bindings[0]}
                  onUpdate={update => {
                    handleUpdateBindings(i, update)
                  }}
                />
              </div>

              <ComboInputMap
                layout={layout}
                keyPositions={combo.keyPositions}
                onUpdate={value => handleUpdateKeyPositions(i, value)}
              />
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}

ComboEditor.propTypes = {
  layout: layoutPropTypes.layout.isRequired
}

export default ComboEditor
