import * as config from './config'

export function healthcheck() {
  return fetch(`${config.apiBaseUrl}/health`)
}

export function loadBehaviourBindings() {
  return fetch(`${config.apiBaseUrl}/behavior-bindings`).then(response => response.json())
}

export function loadKeycodes() {
  return fetch(`${config.apiBaseUrl}/keycodes`).then(response => response.json())
}

export function loadKeycodeGlyphs() {
  return fetch(`${config.apiBaseUrl}/keycode-glyphs`).then(response => response.json())
}

export async function loadKeyboard () {
  const response = await fetch(`${config.apiBaseUrl}/keyboard`)
  const { info, keymap } = await response.json()
  const defaultLayout = (
    info.layouts.default ||
    info.layouts[Object.keys(info.layouts)[0]]
  )

  return {
    layout: defaultLayout.layout,
    keymap,
    sensors: info.sensors
  }
}

export function loadKeymap({ useDevicetree = false } = {}) {
  const url = new URL(`${config.apiBaseUrl}/keymap`)
  const query = new URLSearchParams()

  if (useDevicetree) {
    query.set('useDevicetree', useDevicetree)
  }

  url.search = query.toString()

  return fetch(url)
    .then(response => response.json())
}

export function loadLayout() {
  return fetch(`${config.apiBaseUrl}/layout`)
    .then(response => response.json())
}
