import get from 'lodash/get'
import {
  createContext,
  useCallback,
  useContext,
  useReducer
} from 'react'

import appReducer, { INITIAL_STATE } from '../appReducer'
import keymapReducer from '../Keyboard/reducers'
import combosReducer from '../Keyboard/Combos/reducers'
import macrosReducer from '../Keyboard/Macros/reducer'

// Based on https://codezup.com/how-to-combine-multiple-reducers-in-react-hooks-usereducer/

const Context = createContext()

// Different reducers throughout the codebase are written to manage their own
// state trees that are nested together in one "root" state tree. This reducer
// delegates to the correct "sub" reducer based on the action type. This action
// wraps the actual action (see useAppContext below) passed to the actual reducer.
function rootReducer (state, action) {
  const { type, payload } = action

  switch (type) {
    case 'ROOT':
      return appReducer(state, payload)

    case 'keyboard.keymap':
      return {
        ...state,
        keyboard: {
          ...state.keyboard,
          keymap: keymapReducer(
            state.keyboard.keymap,
            payload
          )
        }
      }

    case 'keyboard.keymap.combos':
      return {
        ...state,
        keyboard: {
          ...state.keyboard,
          keymap: {
            ...state.keyboard.keymap,
            combos: combosReducer(
              state.keyboard.keymap.combos,
              payload
            )
          }
        }
      }

    case 'keyboard.keymap.macros':
      return {
        ...state,
        keyboard: {
          ...state.keyboard,
          keymap: {
            ...state.keyboard.keymap,
            macros: macrosReducer(
              state.keyboard.keymap.macros,
              payload
            )
          }
        }
      }

    default:
      throw new Error('unexpected action', type)
  }
}

// The AppStateProvider creates the state tree and dispatch function based on
// the rootReducer and provides them as context to descendents.
export function AppStateProvider ({ children }) {
  const [state, dispatch] = useReducer(rootReducer, INITIAL_STATE)

  return (
    <Context.Provider value={[state, dispatch]}>
      {children}
    </Context.Provider>
  )
}

// A custom hook to get the state and dispatch functions from AppStateProvider.
// The returned state is the requested part of the state tree, and the dispatch
// function is a facade that passed a wrapped payload to the actual dispatcher.
// The wrapping identifies which sub-reducer to delegate to.
export function useAppState (key = 'ROOT') {
  const [root, dispatch] = useContext(Context)

  const state = key === 'ROOT' ? root : get(root, key)
  const dispatchers = {
    ROOT: useCallback(payload => dispatch({ type: 'ROOT', payload }), [dispatch]),
    'keyboard.keymap': useCallback(payload => dispatch({ type: 'keyboard.keymap', payload }), [dispatch]),
    'keyboard.keymap.combos': useCallback(payload => dispatch({ type: 'keyboard.keymap.combos', payload }), [dispatch]),
    'keyboard.keymap.macros': useCallback(payload => dispatch({ type: 'keyboard.keymap.macros', payload }), [dispatch]),
  }

  return [state, dispatchers[key]]
}
