import {
  withoutIndex,
  replaceIndex,
  repositionElement
} from '../../reducer-util'

export default function reducer (state, action) {
  const { type } = action

  switch (type) {
    case 'ADD_MACRO':
      return [...state, {
        identifier: 'new_macro',
        label: 'new_macro',
        bindings: []
      }]

    case 'RENAME_MACRO':
      return replaceIndex(state, action.index, {
        ...state[action.index],
        identifier: action.name
      })

    case 'DELETE_MACRO':
      return withoutIndex(state, action.index)

    case 'ADD_MACRO_CONTROL_BINDING':
      return replaceIndex(state, action.macroIndex, {
        ...state[action.macroIndex],
        bindings: [
          ...state[action.macroIndex].bindings,
          { value: '&macro_press', params: [] }
        ]
      })

    case 'ADD_MACRO_KEY_BINDING':
      return replaceIndex(state, action.macroIndex, {
        ...state[action.macroIndex],
        bindings: [
          ...state[action.macroIndex].bindings,
          { value: '&kp', params: [] }
        ]
      })

    case 'UPDATE_MACRO_BINDING':
      return replaceIndex(state, action.macroIndex, {
        ...state[action.macroIndex],
        bindings: replaceIndex(
          state[action.macroIndex].bindings,
          action.bindIndex,
          action.binding
        )
      })

    case 'REORDER_MACRO_BINDING':
      return replaceIndex(state, action.macroIndex, {
        ...state[action.macroIndex],
        bindings: repositionElement(
          state[action.macroIndex].bindings,
          action.bindIndex,
          action.targetIndex
        )
      })

    case 'DELETE_MACRO_BINDING':
      return replaceIndex(state, action.macroIndex, {
        ...state[action.macroIndex],
        bindings: withoutIndex(
          state[action.macroIndex].bindings,
          action.bindIndex
        )
      })

    case 'CUSTOMIZE_MACRO_OPTIONS':
      return replaceIndex(state, action.macroIndex, {
        ...state[action.macroIndex],
        ...action.options
      })

    default:
      throw new Error(`unexpected action "${type}"`)
  }
}
