import { useContext } from 'react'

import { useAppState } from '../../contexts/AppState'
import { SettingsContext } from '../../providers'

import Macro from './Macro'
import styles from './styles.module.css'

import ActionButton from '../../Common/ActionButton'
import Link from '../../Common/Link'
import Icon from '../../Common/Icon'
import FeatureRequiresDevicetree from '../../Dialogs/FeatureRequiresDevicetree'

function MacroEditor () {
  const { settings } = useContext(SettingsContext)
  const [macros, dispatch] = useAppState('keyboard.keymap.macros')

  if (settings.legacyKeymap) {
    return (
      <FeatureRequiresDevicetree />
    )
  }

  return (
    <div className={styles.main}>
      <div className={styles.sidebar}>
        <Link external href="https://zmk.dev/docs/behaviors/macros">
          <Icon name="book" /> ZMK Docs: Macros
        </Link>

        <ActionButton onClick={() => dispatch({ type: 'ADD_MACRO' })}>
          Add New Macro
        </ActionButton>

        <div className={styles.betaDisclaimer}>
          <h3>Beta Notes</h3>
          <p>
            Macro editing is still in beta. Please report any issues to the
            project on GitHub.
          </p>
          <p>
            Note that the editor will only look at macros defined in your
            <code>.keymap</code> file at this time, and will not import included
            files like <code>macros.dtsi</code>.
          </p>
        </div>
      </div>

      {macros.map((macro, i) => (
        <Macro
          key={i}
          macro={macro}
          index={i}
          dispatch={dispatch}
        />
      ))}
    </div>
  )
}

export default MacroEditor
